.header {
  height: 60rem;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  img {
    z-index: 4;
    bottom: 0;
    right: 15rem;
    position: absolute;
    height: 36rem;
  }
  .titles {
    width: 60rem;
    z-index: 5;
    max-width: 100%;
    margin-left: 10rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  .subtitle {
    color: white;
    font-size: 2.2rem;
    text-align: center;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 400;
    z-index: 102;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease-in;
    text-shadow: 2px 2px 4px #6c63ff;
    transition: all 0.3s ease-in;
    max-width: 100%;
    &:hover {
      transform: scale(1.05);
      text-shadow: 1px 1px 2px #6c63ff;
    }
  }

  .title {
    max-width: 100%;
    //text-shadow: 2px 2px 4px #6c63ff;
    text-shadow: 2px 2px 4px #6c63ff;
    color: white;
    font-size: 6rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-align: center;
    z-index: 102;
    border: none;
    outline: none;
    margin-bottom: 2rem;
  }
  .spaceVideo {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -100;
    height: 100%;
    width: 100%;
    object-fit: cover;
    overflow: hidden;
  }
}
@media (max-width: 1024px) {
  .header {
    justify-content: center;
    align-items: flex-start;
    img {
      left: 50%;
      transform: translateX(-50%);
    }
    height: 70rem;
    .titles {
      margin: 10rem 0;
      .title {
        font-size: 5rem;
      }
    }
  }
}
